import React from 'react'
import Link, {withPrefix} from 'gatsby-link'
import styled from "styled-components"

import '../css/normalization.css'
import '../css/fontface.css'
import 'bootstrap/dist/css/bootstrap.css';
import '../css/style.css'
const HeaderWrapper = styled.div`
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    padding: 20px 40px;
    header, .menu {
        display: flex;
    }
    .menu {
        justify-content: space-between;
        width: 100%;
        margin-left: 20px;
    }
    a {
        font-family: 'db_heaventlight';
        font-size: 22px;
        color: #FFFFFF;
        letter-spacing: 2.29px;
        line-height: 35px;
    }
`;

class Header extends React.Component {
    render() {
        return (
            <HeaderWrapper>
                <header>
                    <h1>
                        <Link to="/">
                            <img src={__PATH_PREFIX__ + '/static/logo.png'} />
                        </Link>
                    </h1>
                    <div className="menu">
                        <nav className="nav">
                            <ul className="nav justify-content-end">
                                <li className="nav-item">
                                    <Link className="nav-link active" to="/course">Course</Link>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#">One Click</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#">Open House</a>
                                </li>
                            </ul>
                        </nav>
                        <nav className="nav">
                            <ul className="nav justify-content-end">
                                <li className="nav-item">
                                    <a className="nav-link active" href="#">Space</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#">About</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#">Contact</a>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </header>
            </HeaderWrapper>
        )
    }
}

export default Header


